import { Container } from "../../common/Container";
import Header from "../../common/Header";
import Section from "../../common/Section";

const Author = () => (
  <Container>
    <Header title="O autorze" />
    <Section
      title="Piotr Obrochta"
      body={
        <>
          Mieszkam we Wrocawiu, jestem po szkole poligraficznej i ukoczyem j
          jako <strong>Grafik Komputerowy</strong>, mimo wszystko e pracuj w
          drukarni to nie zajmuj stanowiska przy komputerze :) pracuj jako
          operator falcerki, skadam ulotki, skadki do ksiek itp. Zajmuj sie
          obsug maszyn <strong>postpress</strong>
        </>
      }
    />
  </Container>
);

export default Author;
